<template>
  <el-dialog :close-on-click-modal="false" :visible.sync="visible">
    <el-menu
      default-active="1"
      class="el-menu-demo"
      mode="horizontal"
      background-color=""
      text-color=""
      active-text-color="#ffd04b"
    >
      <el-menu-item index="1" @click="showStatus=0"></el-menu-item>
      <el-menu-item index="2" @click="showStatus=1"></el-menu-item>
    </el-menu>
    <el-table :data="foundThings" height="300" border style="width: 100%" v-if="showStatus==1">
      <el-table-column prop="thingsType" label="" width="">
      </el-table-column>
      <el-table-column prop="thingsName" label="" width="">
      </el-table-column>
      <el-table-column prop="pickPlace" label="" width="">
      </el-table-column>
      <el-table-column prop="pickTime" label="" width=""> </el-table-column>
      <el-table-column prop="status" label="" width=""
        >{{ foundThings.status == 1 ? "" : "" }}
      </el-table-column>
    </el-table>
    
    <el-table :data="lostThings" height="300" border style="width: 100%" v-if="showStatus==0">
      <el-table-column prop="thingsType" label="" width="">
      </el-table-column>
      <el-table-column prop="thingsName" label="" width="">
      </el-table-column>
      <el-table-column prop="pickPlace" label="" width="">
      </el-table-column>
      <el-table-column prop="pickTime" label="" width=""> </el-table-column>
      <el-table-column prop="status" label="" width=""
        >{{ lostThings.status == 1 ? "" : "" }}
      </el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      showStatus: 0,
      foundThings: [
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 0,
        },
      ],
      lostThings: [
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 1,
        },
        {
          thingsType: "",
          thingsName: "",
          pickPlace: "",
          pickTime: "2021-03-09",
          status: 0,
        },
      ],
    };
  },
  methods: {
    init() {
      this.visible = true;
    },
  },
};
</script>


