<template>
  <el-dialog
    :title="!dataForm.id ? '' : ''"
    :close-on-click-modal="false"
    :visible.sync="visible"
    width="470px"
  >
    <el-form
      :model="dataForm"
      :rules="dataRule"
      ref="dataForm"
      label-width="50px"
      label-position="left"
      @keyup.enter.native="dataFormSubmit()"
    >
      <el-form-item label="" prop="studentNo">
        <el-input placeholder="" v-model="dataForm.studentNo" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="studentName">
        <el-input
          placeholder=""
          v-model="dataForm.studentName"
          clearable
        >
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="className">
        <el-select
          v-model="dataForm.className"
          clearable
          placeholder=""
        >
          <el-option
            v-for="item in classOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="specialtyName">
        <el-select
          v-model="dataForm.specialtyName"
          clearable
          placeholder=""
        >
          <el-option
            v-for="item in specialtyOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="QQ" prop="qq">
        <el-input placeholder="QQ" v-model="dataForm.qq" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input placeholder="" v-model="dataForm.phone" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input placeholder="" v-model="dataForm.email" clearable>
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          placeholder=""
          v-model="dataForm.password"
          show-password
          clearable
        >
        </el-input>
      </el-form-item>
      <el-form-item label="" prop="repassword">
        <el-input
          placeholder=""
          v-model="dataForm.repassword"
          show-password
          clearable
        >
        </el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="visible = false"></el-button>
      <el-button type="primary" @click="dataFormSubmit()"></el-button>
    </span>
  </el-dialog>
</template>

<script>
// import SingleUpload from "@/components/upload/singleUpload";
export default {
  // components: { SingleUpload },
  data() {
    return {
      visible: false,
      dataForm: {
        thingsName: "",
        thingsType: "",
        pickPlace: "",
        pickTime: "",
        thingsImg: "",
        thingsDes: "",
        storagePlace: "",
        phone: "",
        qq: "",
        email: "",
        publishTime: "",
        studentName: "",
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              picker.$emit("pick", new Date());
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            },
          },
          {
            text: "",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            },
          },
        ],
      },
      dataRule: {
        thingsName: [
          { required: true, message: "", trigger: "blur" },
        ],
        thingsType: [
          { required: true, message: "", trigger: "blur" },
        ],
        pickPlace: [
          { required: true, message: "", trigger: "blur" },
        ],
        pickTime: [
          { required: true, message: "", trigger: "blur" },
        ],
      },

      classOptions: [
        {
          value: "2016",
          label: "2016",
        },
        {
          value: "2017",
          label: "2017",
        },
        {
          value: "2018",
          label: "2018",
        },
        {
          value: "2019",
          label: "2019",
        },
        {
          value: "2020",
          label: "2020",
        },
        {
          value: "2021",
          label: "2021",
        },
      ],
      specialtyOptions: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
      ],
    };
  },
  methods: {
    init(id) {
      this.dataForm.brandId = id || 0;
      this.visible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].resetFields();
        if (this.dataForm.brandId) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/info/${this.dataForm.brandId}`
            ),
            method: "get",
            params: this.$http.adornParams(),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.dataForm.name = data.brand.name;
              this.dataForm.logo = data.brand.logo;
              this.dataForm.descript = data.brand.descript;
              this.dataForm.showStatus = data.brand.showStatus;
              this.dataForm.firstLetter = data.brand.firstLetter;
              this.dataForm.sort = data.brand.sort;
            }
          });
        }
      });
    },
    // 
    dataFormSubmit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          this.$http({
            url: this.$http.adornUrl(
              `/product/brand/${!this.dataForm.brandId ? "save" : "update"}`
            ),
            method: "post",
            data: this.$http.adornData({
              brandId: this.dataForm.brandId || undefined,
              name: this.dataForm.name,
              logo: this.dataForm.logo,
              descript: this.dataForm.descript,
              showStatus: this.dataForm.showStatus,
              firstLetter: this.dataForm.firstLetter,
              sort: this.dataForm.sort,
            }),
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.visible = false;
                  this.$emit("refreshDataList");
                },
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
  },
};
</script>



